<template>
  <div class="auth-wrapper auth-v2">
    <b-row class="auth-inner m-0">



      <!-- Left Text-->
      <b-col
        lg="8"
        class="d-none d-lg-flex auth-bg align-items-center p-5 parent"
        :style="{ backgroundImage:'linear-gradient(rgba(0,0,0,0.7),rgba(0,0,0,0.7)), url(' + imgUrl + ')' }"
      >
      
        
        <div class="w-100 d-lg-flex align-items-center justify-content-center px-5">
          <!-- <b-img
            fluid
            :src="imgUrl"
            alt="Login V2"
          /> -->

          <h1 style="font-weight: bolder; font-size: 50px; color: beige;" >
            Let's start playing the game to better your future
          </h1>
        </div>
      </b-col>
      <!-- /Left Text-->

      <!-- Register-->
      <b-col
        lg="4"
        class="d-flex align-items-center auth-bg px-2 p-lg-5"
      >

         <!-- Brand logo-->
        <b-link class="brand-logo d-flex justify-content-end w-75">
            <vuexy-logo />
        </b-link>
        <!-- /Brand logo-->

        <b-col
          sm="8"
          md="6"
          lg="12"
          class="px-xl-2 mx-auto"
        >
          <b-card-title
            class="mb-1 text-white font-weight-bold"
            title-tag="h2"
          >
            Welcome to The Hustle Lab! 
          </b-card-title>

          <p>
            We are happy to see that you have joined The 
            Hustle Lab!
          </p>

          <p>
            You would have received an invitation code in an 
            email, please enter the code below to sign up
          </p>
          <!-- <b-card-text class="mb-2">
            Make your app management easy and fun!
          </b-card-text> -->

          <!-- form -->
          <validation-observer
            ref="checkMailForm"
            #default="{invalid}"
          >
            <b-form
              class="auth-register-form mt-2"
              @submit.prevent="register"
            >

            
              <!-- username -->
              <b-form-group
                label="Invitation code"
                label-for="checkMail-code"
              >
                <validation-provider
                  #default="{ errors }"
                  name="code"
                  vid="code"
                  rules="required"
                >


                <b-input-group class="input-group-merge">
                  
                  <b-input-group-prepend is-text>
                    <feather-icon icon="CodeIcon" />
                  </b-input-group-prepend>

                  <b-form-input
                    id="checkMail-code"
                    v-model="usercode"
                    name="checkMail-code"
                    :state="errors.length > 0 ? false:null"
                    placeholder="Code"
                  />
                  
                  <b-input-group-append is-text>
                    <feather-icon icon="XIcon"
                    class="cursor-pointer"
                    @click="clearField('code')"
                    />
                  </b-input-group-append>

                </b-input-group>

                  <small class="text-danger">{{ errors[0] }}</small>
                </validation-provider>
              </b-form-group>

          

              <!-- <b-form-group>
                <b-form-checkbox
                  id="register-privacy-policy"
                  v-model="status"
                  name="checkbox-1"
                >
                  I agree to
                  <b-link>privacy policy & terms</b-link>
                </b-form-checkbox>
              </b-form-group> -->

              <b-button
                variant="primary"
                block
                type="submit"
                :disabled="invalid"
                class="mt-2 rounded-pill button-color"
              >
                Sign up
              </b-button>

              <p class="text-center mt-2">
                <span>Already have an account?</span>
                <b-link :to="{name:'auth-login'}">
                <span class="text_pink">&nbsp;Login</span>
                </b-link>
              </p>


            </b-form>
          </validation-observer>


        </b-col>
      </b-col>
    <!-- /Register-->
    </b-row>
  </div>
</template>

<script>
/* eslint-disable global-require */
import { ValidationProvider, ValidationObserver } from 'vee-validate'
import VuexyLogo from '@core/layouts/components/Logo.vue'
import {
  BRow, BCol,BAlert, BLink,BInputGroupPrepend, BButton, BForm, BFormCheckbox, BFormGroup, BFormInput, BInputGroup, BInputGroupAppend, BImg, BCardTitle, BCardText,
} from 'bootstrap-vue'
import { required, email } from '@validations'
import { togglePasswordVisibility } from '@core/mixins/ui/forms'
import store from '@/store/index'
import useJwt from '@/auth/jwt/useJwt'
import Colors from '@/views/ui/colors/Colors.vue'

export default {
  components: {
    VuexyLogo,
    BRow,
    BCol,
    BLink,
    BInputGroupPrepend,
    BFormGroup,
    BFormInput,
    BInputGroupAppend,
    BInputGroup,
    BFormCheckbox,
    BCardText,
    BCardTitle,
    BImg,
    BForm,
    BButton,
    BAlert,
    // validations
    ValidationProvider,
    ValidationObserver,
  },
  mixins: [togglePasswordVisibility],
  data() {
    return {
      status: '',
      usercode: '',
      sideImg: require('@/assets/images/pages/register-v2.svg'),
      // validation
      required,
      email,
    }
  },
  computed: {
    passwordToggleIcon() {
      return this.passwordFieldType === 'password' ? 'EyeIcon' : 'EyeOffIcon'
    },
    imgUrl() {
      if (store.state.appConfig.layout.skin === 'dark') {
        // eslint-disable-next-line vue/no-side-effects-in-computed-properties
        this.sideImg = require('@/assets/images/pages/register-v2-dark.svg')
        return this.sideImg
      }
      return this.sideImg
    },
  },
  methods: {
    clearField(field) {
      // Clear the specified field
      if (field === 'code') {
        this.usercode = '';
      }
    },
    register() {
      this.$refs.registerForm.validate().then(success => {
        if (success) {
          useJwt.register({
            username: this.username,
            email: this.userEmail,
            password: this.password,
          })
            .then(response => {
              useJwt.setToken(response.data.accessToken)
              useJwt.setRefreshToken(response.data.refreshToken)
              localStorage.setItem('userData', JSON.stringify(response.data.userData))
              this.$ability.update(response.data.userData.ability)
              this.$router.push('/')
            })
            .catch(error => {
              this.$refs.registerForm.setErrors(error.response.data.error)
            })
        }
      })
    },
  },
}
/* eslint-disable global-require */
</script>

